
import './App.css';
import { useState } from 'react';
import Card from 'react-bootstrap/Card'
import Image from 'react-bootstrap/Image'
import profile from './images/potrait.jpeg'
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import Button from 'react-bootstrap/Button'
import * as Icon from 'react-bootstrap-icons';
import 'bootstrap/dist/css/bootstrap.min.css'



function App() {
     
 const [count, setCount] = useState(0);
 const handleClick=(e)=>{
    console.log(e.target.value)
    document.getElementById('para').innerHTML = (e.target.value);
  }
  return (
 
    <div className="App">
      <Card className="text-center bg-warning " style={{ width: '30rem', height: '35rem'}}>
        <Image className="mx-auto d-block rounded-circle mt-5" src={profile} style={{  width: '150px', height: '150px'}} alt="profile" />
        <Card.Body>
        <Card.Title>Santhi Sri Jamisetty</Card.Title>
        <div id="para"></div><br /> <br />
         <ButtonGroup onClick={handleClick}>
          <Button  variant="success" value="Salesforce Developer">Job</Button>{'  '}
           <Button   variant="success" value="HTML CSS React">Skills</Button>
           </ButtonGroup>
           <br />
           <br />
           <p>{count} {' '}
           <Button className="heart" onClick={() => setCount(count + 1)}>
                  <Icon.Heart /><br />
            </Button>
            </p>
           <a href="https://twitter.com/sanjami17" target="_blank" rel="noopener noreferrer"
                               className="twitter social">
                <Icon.Twitter size={30} />{' '}
           </a>
           <a href="https://www.linkedin.com/in/santhi-sri-j-a5147014b/" target="_blank" rel="noopener noreferrer"
                               className="linkedin social">
           <Icon.Linkedin size={30} />
           </a>
        </Card.Body>
      </Card>
</div>
  );
}

export default App;
